import React from 'react'
import { Link } from 'react-router-dom'
import Content from './Icon/Icon'
import Style from './Homepage.module.css'
import Icon from './Icon/Icon'

export default function Homepage() {
    return (
        <main className={Style.homepage}>
            {/* The First div */}
            <div className={Style.firstDiv}>
                <div className={`${Style.description} ${Style.description1}`}>
                    <div className={` ${Style.content}${Style.content1}`}>
                        <h1 className={`${Style.title} ${Style.title1}`}>   </h1>
                        <h2 className={`${Style.subtitle} ${Style.subtitle1}`}>     </h2>
                        <Link to="/my-created"><button className={`${Style.button} ${Style.button1}`} >  </button></Link>

                    </div>
                </div>
                <div className={`${Style.img} ${Style.img1}`}>
                    
                </div>
                
            </div>


            <div className={`${Style.secondDiv}`}>
                <div className={`${Style.description} ${Style.description2}`}>
                    <div className={` ${Style.content}${Style.content2}`}>
                        <h1 className={`${Style.title} ${Style.title2}`}>  </h1>
                        <h2 className={`${Style.subtitle} ${Style.subtitle2}`}>               </h2>
                        <Link to="/register"><button className={`${Style.button} ${Style.button2}`} > </button></Link>
                    </div>
                    <div className={Style.iconsLine}>
                        <div className={Style.iconsGroup}>
                            <Icon icon={"fa-running"} text={'    '}/>
                            <Icon icon={"fa-dollar-sign"} text={'    '}/>
                        </div>
                        <div className={Style.iconsGroup}>
                            <Icon icon={"fa-chart-line"} text={'  '}/>
                            <Icon icon={"fa-book-reader"} text={'  '}/>
                        </div>
                    </div>
                </div>
            </div>

            <div className={Style.thirdDiv}>
                <div className={`${Style.img}  ${Style.topLeft}`}></div>
                <div className={`${Style.img} ${Style.topRight}`}>

                    <div className={` ${Style.content} ${Style.content3}`}>
                        <h1 className={`${Style.title} ${Style.title3}`}>  </h1>
                        <h2 className={`${Style.subtitle} ${Style.subtitle3}`}>      </h2>
                        <Link to="/register" ><button className={`${Style.button} ${Style.button3}`} > </button></Link>
                    </div>

                </div>
                <div className={`${Style.img} ${Style.bottomLeft}`}>

                    <div className={` ${Style.content} ${Style.content3}`}>
                        <h1 className={`${Style.title} ${Style.title3}`}> </h1>
                        <h2 className={`${Style.subtitle} ${Style.subtitle3}`}>    </h2>
                        <Link to="/my-tests"><button className={`${Style.button} ${Style.button3}`} > </button></Link>
                    </div>

                </div>
                <div className={`${Style.img} ${Style.bottomRight}`}></div>
            </div>
        </main>
    )
}
